@import '../../components/scss/variables';
.default {
  &.card {
    margin-top: 20px;
    box-shadow: 0 3px 6px #00000029;
    padding: 10px 20px;
    border-radius: 10px;
    background-color: white;
    .main-content {
      padding: 20px 20px;
    }
    h3 {
      font-size: 16px;
    }
    p {
      font-size: 12px;
    }
    @media only screen and (max-width: $tablet) {
      .main-content {
        padding: 20px 0px;
      }
    }
  }
}
